<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('Add Client') }}</h4>
                        <button type="button" class="close" v-on:click="Close()">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import services from "@/services/DataService";
export default {
    data() {
        return {
            isLoading:false,
            TimeZoneList:[],
            ParentAccountList:[],
            CientOwnerList:[],
            RoleList:[],
            FormSchema: [
                {
                    layoutType: "four",
                    Data: [                                
                        {
                            astype: "TextField",
                            label: this.$t('First Name'),
                            name: "First Name",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 100 },                            
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Last Name'),
                            name: "Last Name",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 100 },                            
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Email '),
                            name: "Email",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 100 },                            
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Gender'),
                            name: "Gender",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [{ value: "M", name:"Male" },{ value : "F", name : "Female" }],
                                showAddIcon: false,
                                
                            },
                            validationRules: "",
                        },
                        {
                            astype: "RadioField",
                            label: this.$t('Account'),
                            name: "Account",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [{
                                    name:'Existing',value:1
                                },{
                                    name:'Create New',value:2
                                }],
                                showAddIcon: false,
                                checked: this.onChangeRadio
                            },
                            validationRules: { "required": true},
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Parent Account'),
                            name: "Gender",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                
                            },
                            validationRules: { "required": true},
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Account Name'),
                            name: "Account Name",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": false, max: 100 },                            
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Client Owner'),
                            name: "Client Owner",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                
                            },
                            validationRules: "",
                        },
                       
                    ]
                },
                {
                    group_name: "Advanced Setting",
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('TimeZone'),
                            name: "TimeZone",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                
                            },
                            validationRules: "",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Date Format'),
                            name: "Date Format",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                
                            },
                            validationRules: "",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Role'),
                            name: "Role",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                
                            },
                            validationRules: "",
                        },
                        {
                            astype: "FileField",                        
                            showLabel:false,
                            name: "Attachment",
                            value: "",
                            placeholder: "",
                            validationRules: "",
                            FieldIndex: 0,
                            config: {
                                maxSize:5,
                                onChange: this.setFile,
                                showAddIcon: false,
                                onAddButtonClick: this.AddFileMore,
                                multiple:false
                            },
                        }
                    ]
                },
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",                    
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick:this.Close
                }
            ],
        }
    },
    created:async function () {        
         await this.GetTimeZoneList(); 
         await this.GetParentAccountList();
         await this.GetClientOwnerList();
         await this.GetRoleList();
    },
    methods:{
        Close:function(){
            this.$emit('CloseClientModel');
        },
        GetTimeZoneList:async function(){
            var vm=this;
            await  services.GetTimeZoneList().then(async(response) => {
                 if (response.data.length > 0) {
                   vm.TimeZoneList=response.data;
                   debugger
                   vm.TimeZoneList.forEach((item, index) => {
                    (vm.FormSchema)[1].Data[0].config.options.push({
                        value: item.code,
                        name: item.value
                    })
                })
                 }
            });
        },
        GetParentAccountList:async function(){
            var vm=this;
            await  services.GetParentAccountList().then(async(response) => {
                 if (response.data.length > 0) {
                   vm.ParentAccountList=response.data;
                   vm.ParentAccountList.forEach((item, index) => {
                    (vm.FormSchema)[0].Data[5].config.options.push({
                        value: item.value,
                        name: item.name
                    })
                })
                 }
            });
        },
        GetClientOwnerList:async function(){
            var vm=this;
            var params = `leadId=${0}&type=${'P'}&moduleName=${'Client'}`;
            await  services.GetClientOwnerList(params).then(async(response) => {
                 if (response.data.length > 0) {
                   vm.ParentAccountList=response.data;
                   vm.ParentAccountList.forEach((item, index) => {
                    (vm.FormSchema)[0].Data[7].config.options.push({
                        value: item.value,
                        name: item.name
                    })
                })
                 }
            });
        },
        GetRoleList:async function(){
            debugger
            var vm=this;
            var params = `usertype=${4}`;
            await  services.GetRoleList(params).then(async(response) => {
                debugger
                 if (response.data.length > 0) {
                   vm.RoleListList=response.data;
                   vm.RoleList.forEach((item, index) => {
                    (vm.FormSchema)[1].Data[2].config.options.push({
                        value: item.roleId,
                        name: item.roleName
                    })
                })
                 }
            });
        },
    }
    
    
  

}
</script>